@import url(https://fonts.googleapis.com/css2?family=Caveat:wght@700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App{text-align:center;min-height:100vh;background-color:#2c2c2c;color:#fff;padding:50px}.App h1{margin-top:100px;font-family:'Caveat', cursive}.App .list-section{display:flex;flex-direction:column;align-items:center;justify-content:center}.App .todo-list{list-style-type:none}


.todo-item{font-size:30px;background-color:#3c5252;padding:10px 20px;margin:10px;border:1px solid rgba(109,143,107,0.7)}.todo-item>div{display:flex;flex-direction:row;justify-content:space-between}.todo-item>div .icons{margin-left:30px}.todo-item span{margin-left:20px}.todo-item span:first-child:hover{cursor:pointer;color:rgba(99,248,91,0.7)}.todo-item span:last-child:hover{cursor:pointer;color:rgba(252,105,38,0.7)}.done{text-decoration:line-through}

form{margin:20px}form input[type=text]{padding:8px;border-radius:3px}form input[type=submit]{padding:10px;border-radius:3px;background-color:#686464;color:#fff;border:1px solid #474040}form input[type=submit]:hover{cursor:pointer;background-color:rgba(83,80,80,0.8)}

